@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap";
:root {
  --primary-color: #e9615e;
  --secondary-color: #ec9956;
  --extra-light: #f3f4f6;
  --white: #fff;
  --gradient-1: linear-gradient(to bottom right, #62393c, #181e41);
  --gradient-2: linear-gradient(to bottom, var(--primary-color), var(--secondary-color));
  --max-width: 1200px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.logoEndDiv {
  justify-content: center;
  justify-items: center;
  display: flex;
}

.logoEnd {
  align-self: center;
  width: 200px;
  height: 200px;
}

.section__container {
  max-width: var(--max-width);
  margin: auto;
  padding: 5rem 1rem;
}

.section__header {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: 700;
}

.section__description {
  max-width: 600px;
  color: var(--extra-light);
  text-align: center;
  margin-inline: auto;
  line-height: 1.75rem;
}

.btn {
  color: var(--white);
  background-image: var(--gradient-2);
  cursor: pointer;
  border: none;
  border-radius: 5rem;
  outline: none;
  padding: .75rem 1.5rem;
  font-size: 1rem;
  font-weight: 500;
  box-shadow: 5px 5px 10px #0003;
}

img {
  width: 100%;
  display: flex;
}

a {
  text-decoration: none;
}

.logo {
  color: var(--white);
  background-image: var(--gradient-2);
  border-radius: 100%;
  padding: 10px 14px;
  font-size: 1.5rem;
  font-weight: 700;
  display: inline-block;
  box-shadow: 5px 5px 10px #0003;
}

html, body {
  scroll-behavior: smooth;
}

body {
  color: var(--white);
  background-image: var(--gradient-1);
  font-family: Montserrat, sans-serif;
}

nav {
  isolation: isolate;
  width: 100%;
  max-width: var(--max-width);
  z-index: 9;
  margin: auto;
  position: fixed;
  top: 0;
}

.nav__bar {
  background-image: var(--gradient-1);
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 1rem;
  display: flex;
}

.nav__logo {
  padding: 9px 12px;
  font-size: 1.2rem;
}

.nav__bar a {
  color: var(--white);
  font-size: 1.2rem;
  font-weight: 600;
}

.nav__menu__btn {
  color: var(--white);
  cursor: pointer;
  font-size: 1.5rem;
}

.nav__links {
  background-image: var(--gradient-2);
  z-index: -1;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
  padding: 2rem;
  list-style: none;
  transition: all .5s;
  display: flex;
  position: absolute;
  transform: translateY(-100%);
}

.nav__links.open {
  transform: translateY(0);
}

.nav__links a {
  color: var(--white);
  transition: all .3s;
}

.nav__links a:hover {
  color: var(--white);
}

.btn__large {
  display: none;
}

.header__container {
  isolation: isolate;
  gap: 2rem;
  padding-top: 8rem;
  display: grid;
}

.header__content {
  text-align: center;
}

.header__content h1 {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 3rem;
}

.header__content .section__description {
  margin-bottom: 2rem;
}

.header__image {
  isolation: isolate;
  z-index: -1;
  position: relative;
}

.header__image img {
  border-radius: 25rem;
  max-width: 450px;
  margin-inline: auto;
}

.header__image:after {
  content: "H.O.G";
  letter-spacing: 1.5rem;
  opacity: .5;
  position: absolute;
  top: 50%;
  right: 1rem;
  transform: translate(50%, -50%)rotate(90deg);
}

.about__container {
  gap: 2rem;
  display: grid;
}

.about__image {
  isolation: isolate;
  max-width: 400px;
  margin: auto;
  position: relative;
}

.about__img {
  border-radius: 100%;
  width: 20rem;
  height: 20rem;
}

.about__bg-1, .about__bg-2 {
  z-index: -1;
  max-width: 150px;
  position: absolute;
}

.about__bg-1 {
  top: 0;
  left: 0;
}

.about__bg-2 {
  bottom: 0;
  right: 0;
}

.about__content {
  text-align: center;
}

.about__content .section__description {
  margin-bottom: 2rem;
}

.service__grid {
  gap: 1rem;
  margin-top: 2rem;
  display: grid;
}

.service__card {
  text-align: center;
  background-image: var(--gradient-2);
  border-radius: 1rem;
  padding: 2rem 1rem;
  box-shadow: 5px 5px 10px #0003;
}

.service__card span {
  margin-bottom: 1rem;
  font-size: 2rem;
  display: inline-block;
}

.service__card h4 {
  margin-bottom: 1rem;
  font-size: 1.2rem;
  font-weight: 700;
}

.service__card p {
  color: var(--extra-light);
  line-height: 2rem;
}

.portfolio__grid {
  gap: 1rem;
  margin-top: 2rem;
  display: grid;
}

.portfolio__card {
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 5px 5px 10px #0003;
}

.portfolio__card img {
  transition: all .5s;
}

.portfolio__card:hover img {
  transform: scale(1.1);
}

.portfolio__banner {
  text-align: center;
  background-image: var(--gradient-2);
  border-radius: 1rem;
  gap: 2rem;
  margin-top: 2rem;
  padding: 2rem;
  display: grid;
  box-shadow: 5px 5px 10px #0003;
}

.portfolio__banner__card span {
  margin-bottom: .5rem;
  font-size: 1.5rem;
  display: inline-block;
}

.portfolio__banner__card h4 {
  font-size: 1.2rem;
  font-weight: 600;
}

.portfolio__banner__card p {
  font-weight: 500;
}

.contact__container {
  text-align: center;
}

.contact__container .logo {
  margin-bottom: 1rem;
}

.contact__socials {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 2rem;
  display: flex;
}

.contact__socials a {
  color: var(--white);
  background: #fff3;
  border-radius: 100%;
  padding: 7px 10px;
  font-size: 1.5rem;
  transition: all .3s;
  box-shadow: 5px 5px 10px #0000001a;
}

.contact__socials a:hover {
  background: var(--gradient-2);
  box-shadow: 5px 5px 10px #0003;
}

.footer {
  color: var(--extra-light);
  text-align: center;
  padding: 1rem;
  font-size: .9rem;
}

@media (width > 576px) {
  .service__grid, .portfolio__grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .portfolio__banner {
    text-align: left;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width > 768px) {
  nav {
    justify-content: space-between;
    align-items: center;
    padding: 2rem 1rem;
    display: flex;
    position: static;
  }

  .nav__bar {
    background-image: none;
    padding: 0;
  }

  .nav__menu__btn {
    display: none;
  }

  .nav__links {
    width: unset;
    background-image: none;
    flex-direction: row;
    padding: 0;
    position: static;
    transform: none;
  }

  .nav__links a:hover {
    color: var(--secondary-color);
  }

  .nav__links li:last-child {
    display: none;
  }

  .btn__large {
    display: flex;
  }

  .header__container {
    grid-template-columns: repeat(4, 1fr);
    align-items: center;
    padding-top: 5rem;
  }

  .header__image {
    grid-area: 1 / 3 / 2 / 5;
  }

  .header__image img {
    margin-inline-start: unset;
  }

  .header__content {
    text-align: left;
    grid-area: 1 / 1 / 2 / 4;
  }

  .header__content h1 {
    text-align: left;
    font-size: 6rem;
    line-height: 6rem;
  }

  .header__content .section__description {
    text-align: left;
    margin-inline-start: unset;
  }

  .about__container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }

  .about__content, .about__content :is(.section__header, .section__description) {
    text-align: left;
  }

  .service__grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .portfolio__container :is(.section__header, .section__description) {
    text-align: left;
    margin-inline-start: unset;
  }

  .portfolio__grid, .portfolio__banner {
    grid-template-columns: repeat(3, 1fr);
  }
}

.MonTueThurDiv, .WednesdayDiv, .thursdayDiv, .FridayDiv {
  border-style: inset;
  border-color: var(--secondary-color);
  background-color: #ffffff1e;
  border-radius: 20px;
  place-self: center;
  width: 90%;
  margin-bottom: 10px;
  padding-top: 10px;
}

.ImgDiv {
  place-self: center;
  max-width: 100%;
  padding: 20px;
  display: flex;
  overflow-y: auto;
}

.wearImg {
  border-radius: 20px;
  width: 250px;
  height: 250px;
}

.wearImgDiv {
  background: var(--secondary-color);
  place-content: center;
  place-items: center;
  width: 265px;
  height: 265px;
  margin-left: 10px;
  display: flex;
}

.NurPriGalleryCont {
  display: flex;
}

.NurPriImgDiv {
  background-color: #fff3;
  justify-self: center;
  place-items: center;
  width: 180px;
  height: 200px;
  padding: 5px;
  font-size: small;
}

.NurPriImgDiv img {
  width: 165px;
  height: 160px;
}

.overflowNurPRImgsDiv {
  border: 1px inset;
  border-color: var(--secondary-color);
  background-color: #62393c34;
  border-radius: 0 20px 20px 0;
  place-content: center;
  align-self: center;
  width: 200px;
  height: 190px;
  overflow-x: auto;
}

.overflowNurPRImgsDiv img {
  width: 180px;
  height: 180px;
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 5px;
}

.eliteStudentImgDiv {
  max-width: 100%;
  display: flex;
  overflow-x: auto;
}

.eliteStudentImgDiv img {
  max-width: 100%;
  height: auto;
  max-height: 30%;
}

.hidden {
  display: none;
}

.proprietress {
  background: radial-gradient(circle, #f6d365, #fda085);
  border-radius: 50%;
  place-content: center;
  place-self: center;
  place-items: center;
  width: 400px;
  height: 400px;
  margin-top: 20px;
  animation: 3s ease-in-out infinite glow;
  display: flex;
  position: relative;
  overflow: hidden;
}

.proprietress img {
  border-radius: 50%;
  width: 90%;
  height: 90%;
  animation: 6s ease-in-out infinite breathe, 20s linear infinite rotate;
}

@keyframes breathe {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.08);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes glow {
  0% {
    box-shadow: 0 0 15px #ffc89680, 0 0 30px #ff96644d;
  }

  50% {
    box-shadow: 0 0 25px #ffc896cc, 0 0 50px #ff966480;
  }

  100% {
    box-shadow: 0 0 15px #ffc89680, 0 0 30px #ff96644d;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(1deg);
  }

  100% {
    transform: rotate(0);
  }
}
